body{
    display: flex;
    flex-direction: column;
    row-gap: 1cm;
}
.first-section-body{
    margin-left: 5vw;
    /* margin-right: 5vw; */
    display: flex;
    justify-content: space-between;
    /* column-gap: 2cm; */
    padding-top: 5vh;
    padding-bottom: 5vh;
   
}
.first-section-body-image{
    padding-left: 10vw;
            width: 30vw;
}
.first-section-body-text{
    width: 50vw;
    text-align: left;
    padding-left: 5vw;

}

.second-section-body{
    display: flex;
    justify-content: space-around;
    margin-left: 5vw;
    margin-right: 5vw;
    padding-top: 5vh;
    padding-bottom: 5vh;
}
.second-section-body-image{
    width: 25vw;
}
.second-section-body-text{
    width: 40vw;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    /* padding-top: 10vh; */
    padding-left: 5vw;
    padding-right: 2vw;
}
.third-section-body-container{
    background-color: #28282b;
}
.third-section-body{
    display: flex;
    padding-right: 10vw;
    justify-content: space-between;
    background-color: #28282b;
    color: whitesmoke;
}
.third-section-body-text{
    width: 50vw;
    text-align: left;
}

.third-section-body-image{
    width: 30vw;
}

@media screen and (max-width : 1000px){
    .first-section-body-image{
        display: none;
    }
    .first-section-body-text{
        width: 100vw;
        padding-right: 10vw;
        text-align: center;
    }
    .second-section-body-image{
        display: none;
    }
    .second-section-body-text{
        width: 100vw;
        padding-right: 5vw;
        text-align: center;
    }
    .third-section-body-image{
        display: none;
    }
    .third-section-body-text{
        width: 100vw;
        /* padding-right: 10vw; */
        text-align: center;
        padding-left: 10vw;
    }
}

@media screen and (max-width : 600px){
    
    .first-section-body-text{
        font-size: 28px;
    }
    
    .second-section-body-text{
        font-size: 23px;
    }
    
    .third-section-body-text{
        font-size: 28px;
    }
}
@media screen and (max-width : 450px){
    
    .first-section-body-text{
        font-size: 25px;
    }
    
    .second-section-body-text{
        font-size: 21px;
    }
    
    .third-section-body-text{
        font-size: 25px;
    }
}
@media screen and (max-width : 350px){
    
    .first-section-body-text{
        font-size: 23px;
    }
    
    .second-section-body-text{
        font-size: 20px;
    }
    
    .third-section-body-text{
        font-size: 23px;
    }
}