.navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 5vw;
    margin-right: 5vw;
    border-bottom: 1px solid gray;
    background-color: whitesmoke;
}
.logo-section{
    margin-left:5vw;
}
.menu-section{
    display: flex;
    column-gap: .5cm;
    margin-right: 5vw;
}
.menu-img{
    width: 1cm;
    height: 1cm;
    padding-top: 1vh;
    display: none;
}

@media screen and (max-width : 1000px){
    .menu-img{
        display: block;
    }
    .menu-item{
        display: none;
    }
}